body {
  font-family: 'Mukta', sans-serif;
  font-size: 0.8rem;
  color: #333333;
  overflow-y: scroll;
  overflow-x: hidden; }

img {
  max-width: 100%;
  height: auto; }

* {
  box-sizing: border-box; }

li > a {
  text-decoration: none;
  color: #333333; }

ul,
p {
  line-height: 2; }

hr {
  position: absolute;
  display: block;
  border-top: 1px solid #333333;
  border-left: 0;
  border-right: 0;
  border-bottom: 0;
  width: 90px;
  float: left; }

.images-animation-enter {
  opacity: 0.01;
  transform: scale(1.1); }

.images-animation-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 1000ms; }

.images-animation-exit {
  opacity: 1;
  transform: scale(1); }

.images-animation-exit-active {
  opacity: 0.01;
  transform: scale(1.1);
  transition: all 1000ms; }

.image-enter {
  opacity: 0.01;
  transform: translateX(-100px); }

.image-enter-active {
  opacity: 1;
  transform: translateX(0px);
  transition-delay: 0.25s;
  /* wait a bit for space to be made */
  transition: all 500ms; }

.image-exit {
  opacity: 1;
  transform: translateX(0px); }

.image-exit-active {
  opacity: 0.01;
  transform: translateX(100px);
  transition: all 500ms; }

.social-icons {
  text-align: left;
  position: absolute;
  bottom: 1rem;
  margin-bottom: 3.5rem;
  margin-left: 30px; }
  .social-icons__icon {
    display: inline-block;
    position: relative;
    padding: 7px; }
    @media (min-width: 455px) {
      .social-icons__icon {
        width: 32px; } }
    .social-icons__icon .icon {
      display: block;
      fill: #333; }
  @media (min-width: 700px) {
    .social-icons {
      position: absolute;
      bottom: 4rem;
      margin-bottom: 4rem;
      margin-left: -7px; } }
  @media (min-width: 1010px) {
    .social-icons {
      margin-bottom: 8rem;
      margin-left: -7px; } }

button.btn {
  position: relative;
  padding: 0;
  background-color: transparent;
  color: #eeb57b;
  height: 2rem;
  width: 30%;
  float: right;
  cursor: pointer;
  outline: none;
  box-shadow: none;
  border: 2px solid #eeb57b;
  transition: all 0.2s ease-out; }
  button.btn:hover, button.btn:active {
    background-color: #eeb57b;
    color: #fff;
    border: transparent; }
  button.btn--right {
    margin-left: auto; }

@media (min-width: 455px) {
  .wrapper {
    padding: calc(4.25rem - 2rem) calc(4.25rem - 2rem) 0 calc(4.25rem - 2rem); } }

@media (min-width: 1010px) {
  .wrapper {
    padding: 4.25rem 4.25rem 0 4.25rem; } }

.sidebar {
