@keyframes spinner {
  to {
    transform: rotate(360deg); } }

.spinner:before {
  content: '';
  box-sizing: border-box;
  position: absolute;
  width: 50px;
  height: 50px;
  margin-top: -10px;
  margin-left: -10px;
  border-radius: 50%;
  border: 4px solid #fff;
  border-top-color: #333333;
  animation: spinner 0.6s linear infinite; }
